.mainContainer {
    margin-top: 50px;
    margin-bottom: 60px;
}

.poster {
    width: 150px;
}

.moviesCell {
    cursor: pointer;
    display: flex;
    flex-direction: row;
}

.moviesCell-text {
    padding: 15px;
    align-self: center;
    flex-basis: 70%;
    flex-grow: 4;
}

.moviesCell-img {
    flex-basis: 10%;
    flex-grow: 1;
}

iframe {
    margin: 0 auto;
    display: block;
    width: 100%;
}

h1, h2, p{
    word-wrap: break-word;
    width: 100%;
    white-space: normal !important;
}
